export default {
	data() {
		return {
			useTime: 15,
			errNum: 10,
			num: 3, //当前成绩的游戏模式 ：例如：5*5
			user_grade:[]
		}
	},
	onLoad(e) {
		this.num = e.num;
		let that = this;
		// 读取数据
		uni.getStorage({
			key:'user_grade',
			success:function(res){
				that.user_grade = res.data[0];
			}
		})
	},
	methods: {
		again() {
			uni.redirectTo({
				url: '../play/play?num=' + this.num +'&userAgeType='+this.user_grade.userAgeType
			});
		},
		back() {
			uni.navigateTo({
				url:'../index/index'
			});
		},
		history() {
			uni.navigateTo({
				url: '../history/history?num=' + this.num
			});
		},
		standard() {
			uni.navigateTo({
				url: '../criterion/criterion?num=' + this.num
			});
		}
	}
}
